* {
  box-sizing: border-box;
  margin: 0;
}

.Container {
  margin: 0 auto;
  width: 80%;
  margin-right: 100px;
}

@media only screen and (max-width: 1024px) {
  .Container {
    margin: 0;
  }
}


@media only screen and (max-width: 850px) {
  .Container {
    margin: 0;
    width: 100%;
  }
}

